.twisted__eggroll__sections{
    @apply container mx-auto max-w-[1100px];
}

.twisted__eggroll__sections h1{
    @apply text-3xl font-medium mt-2 mb-2;
}

.twisted__eggroll__sections .chips__sec{
    @apply flex items-center gap-2 pt-4; 
}
.twisted__eggroll__sections .chips__sec .chi__ps{
    @apply flex items-center justify-center w-auto px-4 rounded-full h-7 bg-yellow-300; 
}
.twisted__eggroll__sections .chips__sec .chi__ps span{
    @apply text-sm font-medium;
}

.days__sections{
    @apply flex gap-6;
}
.invest__buttons{
    @apply bg-yellow-400 mt-4 w-80 py-3 font-medium text-base rounded;
}
  