.investor__header__main{
    @apply bg-white p-5;
}

.investor__header__main .investor__navigations__container{
    @apply container mx-auto flex items-center justify-around;
}

.investor__header__main .investor__navigations__container ul{
    @apply flex items-center space-x-6 font-bold;
    font-family: 'Raleway', sans-serif;
}
.investor__header__main .investor__navigations__container ul li{
    @apply text-base hover:text-yellow-400 transition-all ease-linear;
}
.investor__header__main .investor__navigations__container .investor__signin{
    @apply bg-yellow-300 px-8 py-2 hover:bg-black font-medium text-base hover:text-yellow-400 transition-all ease-linear;
}
nav #icon__menu{
    @apply float-right;
    display: none;
}
nav .close__menu{
    display: none;
}

/* Responsive Header */
@media (max-width: 1048px) {
    
}
@media (max-width: 909px) {

    .investor__header__main .investor__navigations__container .logo img{
        width: 100px!important;
    }
    .investor__header__main .investor__navigations__container{
        justify-content: flex-start!important;
    }
    nav #icon__menu{
        display: block;
        position: absolute!important;
        right: 10px!important;
    }

    nav ul {
        @apply fixed w-full h-screen bg-white top-0 overflow-hidden transition-all ease-linear text-center;
        right: -100%;
        padding-top: 4rem!important;
    }
    nav ul {
        display: flex!important;
        flex-direction: column!important;
    }
    .investor__header__main .investor__navigations__container ul li{
        line-height: 50px!important;
    }
    .investor__header__main .investor__navigations__container .investor__signin{
        width: 276px!important;
        border-radius: 50px;
    }
    .show__nav{
        @apply right-0 z-50;
    }
    .banner__bg__contauner{
        padding-left: 10px!important;
    }
    .banner__bg .banner__bg__contauner h1{
    font-size: 32px!important;
    line-height: 43px!important;
    }

    nav .close__menu{
        display: block;
        position: absolute;
        right: 14px;
        top: 11px;
    }
}