:root{
    --shd: 0.00px 4.00px 0px 0px rgba(89,74,65,0.5);
}
.banner__bg{
    background-image: linear-gradient(rgba(0,0,0,0.2),rgba(0,0,0,0.2)), url('../../assets/images/others/banner.webp');
    @apply bg-center bg-cover bg-no-repeat min-h-screen;
}

.banner__bg .banner__bg__contauner{
    @apply container mx-auto pt-56 pl-32;
}

.banner__bg .banner__bg__contauner h1{
    @apply text-6xl text-white font-medium mb-6;
    line-height: 73px;
    text-shadow: 1px 1px rgba(0,0,0,0.4);
}

.banner__bg .banner__bg__contauner p{
    @apply text-white;
    text-shadow: 1px 1px rgba(0,0,0,0.4);
}
.banner__bg .banner__bg__contauner .find__investment__btn{
    @apply text-black font-medium bg-yellow-300 px-10 mt-8 hover:bg-black hover:text-yellow-500 transition-all ease-linear py-3;
}

/* logo__banner */
.logo__banner{
    @apply bg-gray-50 w-full p-4;
}

.logo__banner .logo__banner__container{
    @apply container mx-auto flex justify-center;
}

/* small__busniess__sec */
.small__busniess__sec{
    @apply bg-gray-100 p-8;
}

.small__busniess__sec h1{
    @apply text-4xl font-bold;
}

.small__busniess__sec .images__card__geri{
    @apply grid grid-cols-2 gap-4 px-6 py-8 md:grid-cols-3;
}

.small__busniess__sec .explore__btnsmore{
    @apply px-10 py-3 bg-yellow-500 font-medium hover:bg-black hover:text-yellow-400 transition-all ease-linear duration-200;
    box-shadow: var(--shd,0 1px 4px rgba(0,0,0,.6));
}

/* invests__with__you__sec */
.invests__with__you__sec{
    @apply bg-gray-50 p-10;
}
.invests__with__you__sec h1{
    @apply text-4xl font-medium mb-4;
}
.invests__with__you__sec p{
    @apply text-xl text-center;
}

.invests__with__you__sec .team__circle__image{
    @apply grid grid-cols-2 gap-4 px-6 py-8 md:grid-cols-7;
}

/* youre__in__good__comapny */
.youre__in__good__comapny{
    @apply bg-white p-10;
}

.youre__in__good__comapny h1{
    @apply text-4xl font-medium mb-4;
}
.youre__in__good__comapny p{
    @apply text-xl text-center;
}

.you__are__good__company__grif{
    @apply grid grid-cols-2 gap-16 px-6 py-8 md:grid-cols-4;
}
.you__are__good__company__grif .images__csd{
    @apply relative;
}
.you__are__good__company__grif .images__csd .details__des{
    @apply bg-[#FFDE17] absolute -bottom-4 w-[170px] left-3 h-10 flex justify-center items-center;
}
.you__are__good__company__grif .images__csd .details__des p{
    @apply text-xs font-medium;
}

/* find__your__busniess__next__sec */
.find__your__busniess__next__sec{
    @apply bg-gray-50 p-10 pt-14;
}

.find__your__busniess__next__sec .launch__a__compaugns{
    @apply px-10 py-3 bg-yellow-400 font-medium hover:bg-black hover:text-yellow-400 transition-all ease-linear duration-200;
    box-shadow: var(--shd,0 1px 4px rgba(0,0,0,.6));
}


@media (max-width: 909px){
    .banner__bg{
        min-height: auto!important;
        padding-bottom: 20px!important;
    }
    .banner__bg__contauner{
        padding-top: 5px!important;
    }
    .youre__in__good__comapny{
        padding: 10px!important;
    }

    .you__are__good__company__grif .images__csd .details__des{
        width: 120px!important;
        left: 0!important;
    }
    .you__are__good__company__grif .images__csd .details__des p{
        font-size: 10px!important;
    }
    .youre__in__good__comapny p{
        font-size: 14px!important;
    }
    .you__are__good__company__grif{
        padding-left: 0px!important;
    }
    .team__circle__image{
        padding-left: 0px!important;
    }
    .past__performance{
        margin-left: 2px!important;
    }
    .find__your__busniess__next__sec .flex__row{
        display: flex!important;
        flex-direction: column!important;
    }
    .find__your__busniess__next__sec .flex__row h1{
        font-size: 14px!important;
    }
    .find__your__busniess__next__sec .flex__row p{
        width: 100%!important;
        font-size: 12px!important;

    }

    .small__busniess__sec .explore__btnsmore{
        padding-left: 24px!important;
    }
    .invests__with__you__sec h1{
        font-size: 19px!important;
    }

    .invests__with__you__sec{
        padding-left: 10px!important;
        padding-right: 10px!important;
    }
 }